.Preloader_loader__OR0Ar {
  width: 7em;
  height: 7em;
  font-size: 25px;
  box-sizing: border-box;
  border-top: 0.3em solid #9f5eff;
  border-radius: 50%;
  position: relative;
  animation: Preloader_rotating__2E9GV 2s ease-in-out infinite;
  --direction: 1;
}

.Preloader_loader__OR0Ar::before,
.Preloader_loader__OR0Ar::after {
  content: '';
  position: absolute;
  width: inherit;
  height: inherit;
  border-radius: 50%;
  box-sizing: border-box;
  top: -0.2em;
}

.Preloader_loader__OR0Ar::before {
  border-top: 0.3em solid #ff3b83;
  transform: rotate(120deg);
}

.Preloader_loader__OR0Ar::after {
  border-top: 0.3em solid #ffa726;
  transform: rotate(240deg);
}

.Preloader_loader__OR0Ar span {
  position: absolute;
  color: white;
  width: inherit;
  height: inherit;
  text-align: center;
  line-height: 7em;

  animation: Preloader_rotating__2E9GV 2s linear infinite;
  --direction: -1;
}

@keyframes Preloader_rotating__2E9GV {
  50% {
    transform: rotate(calc(180deg * var(--direction)));
  }

  100% {
    transform: rotate(calc(360deg * var(--direction)));
  }
}

